<template>
  <div class="answer-page-container">
    <div class="answer-page-content">
      <!-- <div v-for="(item, index) in dataList" :key="item"> -->

      <div v-if="status == 'study'">
        <div v-for="(item, index) in medieList[currentMediePage - 1]" :key="index">
          <div class="video-container" v-if="item.dataTeype == 'A'">
            <video
              :src="
                'http://120.224.10.23:18060/navsysBoot/sys/common/static/' +
                item.learnUrl
              "
              controls
              style="width: 98%; height: 700px; margin-bottom: 30px"
            ></video>
            <div class="video-num">视频{{ type[item.sortNum] }}</div>
          </div>

          <div class="img-pdf-content" v-if="item.dataTeype == 'B'">
            <div>
              <!-- <img src="../assets/images/PDF-icon-1.png" /> -->
              <div class="pdf-title" @click="showFile(item)">
                {{ item.title }}
              </div>
              <div class="video-num video-num1">文件{{ type[item.sortNum] }}</div>
              <!-- <div class="showImg" @click="showFile(item)">点击查看文件</div> -->
            </div>
          </div>
        </div>
        <div class="pagination">
          <el-pagination
            background
            :current-page.sync="currentMediePage"
            layout="prev, pager, next"
            :total="medieList.length * 10"
            prev-text="上一页"
            next-text="下一页"
          ></el-pagination>
        </div>
      </div>

      <div v-if="status == 'answer'">
        <div class="questions">
          <div
            class="question-box"
            v-for="(item, index) in dataList[currentPage - 1]"
            :key="index"
          >
            <div v-if="item.dataTeype == 'C'">
              <div class="question-title">
                {{ item.sortNum + 1 }}.{{ item.title }}
              </div>
              <el-radio-group
                v-model="item.checked"
                v-if="item.checkType == '1'"
                style="width: 100%"
              >
                <el-radio
                  v-for="(_item, _index) in item.quesDetailList"
                  :key="_index"
                  :label="_item.detailId"
                  style="width: 23%"
                >
                  {{ _item.content }}
                </el-radio>
              </el-radio-group>
              <el-checkbox-group
                v-model="item.checked"
                v-else
                style="width: 100%"
              >
                <el-checkbox
                  v-for="(_item, _index) in item.quesDetailList"
                  :key="_index"
                  :label="_item.detailId"
                  style="width: 23%"
                >
                  {{ _item.content }}
                </el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </div>
        <!-- </div> -->

        <div class="pagination">
          <el-pagination
            background
            :current-page.sync="currentPage"
            layout="prev, pager, next"
            :total="dataList.length * 10"
            prev-text="上一页"
            next-text="下一页"
          ></el-pagination>
        </div>
      </div>

      <div class="bottom">
        <div class="bottom-left">
          <div class="avator"></div>
          <div class="name">
            <p class="name-text">张三李四</p>
            <p class="name-type">个人用户</p>
          </div>
        </div>
        <div class="bottom-center" @click="submit">
          {{ status == "study" ? "开始答题" : "结束答题" }}
        </div>
        <div class="empty" v-if="status == 'answer'"></div>
        <div class="bottom-right" v-else>
          <p>已经学习时间</p>
          <p>{{ one }}:{{ two }}:{{ three }}</p>
        </div>
      </div>
    </div>

    <el-dialog
      :visible.sync="dialogVisible"
      width="74%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      custom-class="custom-dialog"
    >
      <i class="el-icon-circle-close" @click="dialogVisible = false"></i>
      <div v-if="currentShowType === 'img'">
        <img style="width: 100%" :src="url" />
      </div>

      <div
        v-else-if="currentShowType == 'pdf'"
        class="dialog-body-content-base-style"
        style="justify-content: center; align-items: center"
      >
        <!-- <iframe :src="currentPdfUrl" frameborder="0"></iframe> -->
        <embed :src="currentPdfUrl" style="width: 100%; height: 100%" />
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  props: {},
  components: {
    // videoPlayer,
  },
  data() {
    return {
      type: {
        0: "一",
        1: "二",
        2: "三",
        3: "四",
        4: "五",
        5: "六",
        6: "七",
        7: "八",
        8: "九",
      },
      currentMediePage: 1,
      currentPage: 1,
      flag: null,
      one: "00", // 时
      two: "00", // 分
      three: "00", // 秒
      abc: 0, // 秒的计数
      cde: 0, // 分的计数
      efg: 0, // 时的计数
      dialogVisible: false,
      currentShowType: "pdf",
      currentPdfUrl: "",
      medieList: [],
      dataList: [],
      status: "study",
    };
  },
  methods: {
    startHandler() {
      this.flag = setInterval(() => {
        if (this.three === 60 || this.three === "60") {
          this.three = "00";
          this.abc = 0;
          if (this.two === 60 || this.two === "60") {
            this.two = "00";
            this.cde = 0;
            if (this.efg + 1 <= 9) {
              this.efg++;
              this.one = "0" + this.efg;
            } else {
              this.efg++;
              this.one = this.efg;
            }
          } else {
            if (this.cde + 1 <= 9) {
              this.cde++;
              this.two = "0" + this.cde;
            } else {
              this.cde++;
              this.two = this.cde;
            }
          }
        } else {
          if (this.abc + 1 <= 9) {
            this.abc++;
            this.three = "0" + this.abc;
          } else {
            this.abc++;
            this.three = this.abc;
          }
        }
      }, 1000);
    },

    showFile(item) {
      if (item.dataTeype == "B") {
        this.currentShowType = "pdf";
        this.currentPdfUrl =
          "./pdf/web/viewer.html?file=http://120.224.10.23:18060/navsysBoot/sys/common/static/" +
          item.learnUrl;
      }
      this.dialogVisible = true;
    },

    getQuestionIndex(item) {
      const index = this.dataList[this.currentPage - 1]
        .filter((_i) => _i.dataTeype == "C")
        .findIndex((i) => i.id == item.id);
      return index + 1;
    },

    submit() {
      if (this.status == "study") {
        this.status = "answer";
        document.documentElement.scrollTop = 0;
      } else if (this.status == "answer") {
        // 提交答案
        const answers = {};
        for (let item of this.dataList) {
          for (let i of item) {
            if (i.dataTeype == "C") {
              if (!i.checked) {
                this.$message.error("未完成答题");
                return;
              } else {
                answers[i.id] = i.checked;
              }
            }
          }
        }
      }
    },
  },

  async created() {
    // 开始计时
    this.startHandler();

    let res = await this.$api.get("/navsysBoot/navFront/list");
    if (res && res.success) {
      let objArr1 = [],
        objArr2 = [],
        videoNum = 0,
        pdfNum = 0,
        qusNum = 0;
      (res.result || []).forEach((item) => {
        if (["A", "B"].includes(item.dataTeype)) {
          if (item.dataTeype == "A") {
            item.sortNum = videoNum;
            videoNum++;
          }
          if (item.dataTeype == "B") {
            item.sortNum = pdfNum;
            pdfNum++;
          }
          if (objArr2.length < 3) {
            objArr2.push(item);
          } else {
            this.medieList.push(objArr2);
            objArr2 = [];
            objArr2.push(item);
          }
        } else if (item.dataTeype == "C") {
          item.sortNum = qusNum;
          qusNum++;
          if (!item.checked && item.checkType == "2") {
            this.$set(item, "checked", []);
          }
          if (objArr1.length < 5) {
            objArr1.push(item);
          } else {
            this.dataList.push(objArr1);
            objArr1 = [];
            objArr1.push(item);
          }
        }
      });
      if (objArr1.length) {
        this.dataList.push(objArr1);
      }
      if (objArr2.length) {
        this.medieList.push(objArr2);
      }
      console.log('ll', this.dataList, this.medieList);
    }
  },

  mounted() {},

  computed: {},

  watch: {},
};
</script>

<style lang="scss" scoped>
.answer-page-container {
  height: 3414px;
  background-image: url("../assets/images/bg2.png");
  padding: 374px 95px 363px 95px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  // overflow: auto;

  .answer-page-content {
    width: 100%;
    height: 100%;

    .img-pdf-content {
      display: flex;
      justify-content: center;

      .img-box + .img-box {
        margin-left: 80px;
      }

      .pdf-title {
        // width: 350px;
        height: 120px;
        line-height: 120px;
        padding: 0 30px;
        background: #ffffff;
        border-radius: 20px;
        font-weight: 600;
        font-size: 40px;
        color: #a80407;
        text-align: center;
        cursor: pointer;
        margin: 30px 0;
      }

      .showImg {
        margin: 30px auto 0;
        width: 271px;
        height: 72px;
        line-height: 72px;
        text-align: center;
        border-radius: 34px;
        border: 1px solid #a80407;
        font-size: 32px;
        color: #a80407;
        cursor: pointer;
      }
    }

    .video-container {
      width: 100%;
    }

    .video-num {
      width: 180px;
      height: 68px;
      line-height: 68px;
      text-align: center;
      border-radius: 34px;
      background-color: #fff;
      margin: 0 auto 30px;
      font-size: 32px;
      color: #333;
    }

    .video-num1 {
      margin-bottom: 60px;
    }

    .questions {
      margin-top: 80px;

      .question-box {
        margin-bottom: 150px;

        .question-title {
          font-size: 40px;
          font-weight: 500;
          color: #333;
          margin-bottom: 35px;
        }

        ::v-deep .el-radio__inner:hover,
        ::v-deep .el-checkbox__inner:hover {
          border-color: #d70810;
        }

        ::v-deep .el-radio__input .el-radio__inner,
        ::v-deep .el-checkbox__input .el-checkbox__inner {
          width: 50px;
          height: 50px;
        }

        ::v-deep .el-radio,
        ::v-deep .el-checkbox {
          display: flex;
          align-items: center;
        }

        ::v-deep .el-radio__input.is-checked .el-radio__inner,
        ::v-deep .el-checkbox__input.is-checked .el-checkbox__inner {
          background-color: #d70810;
          border-color: #d70810;
        }

        ::v-deep .el-radio__input + .el-radio__label,
        ::v-deep .el-checkbox__input + .el-checkbox__label {
          font-size: 36px;
          padding-left: 20px;
          width: 100% !important;
          text-overflow: ellipsis;
          white-space: normal;
        }

        ::v-deep .el-radio__input.is-checked + .el-radio__label,
        ::v-deep .el-checkbox__input.is-checked + .el-checkbox__label {
          color: #d70810;
        }
      }
    }

    .pagination {
      text-align: center;
      margin-bottom: 170px;

      ::v-deep .el-pagination {
        .el-pager li.active {
          background-color: #d70810;
        }
        .btn-prev,
        .btn-next {
          background-color: transparent;
          margin: 0 12px;
        }
        span {
          width: 150px;
          height: 80px;
          line-height: 80px;
          border-radius: 20px;
          border: 2px solid #fff;
          font-size: 30px;
          font-weight: 500;
          color: #fff;
        }
        li {
          width: 80px;
          height: 80px;
          line-height: 80px;
          background-color: rgba(255, 255, 255, 0.2);
          border-radius: 20px;
          border: 2px solid #fff;
          font-size: 40px;
          font-weight: 500;
          color: #fff;
          margin: 0 12px;
        }
      }
    }

    .bottom {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .bottom-left {
        display: flex;
        .avator {
          display: inline-block;
          width: 120px;
          height: 120px;
          border: 5px solid #ffffff;
          margin-right: 20px;
        }
        .name {
          display: inline-block;
          height: 120px;
          margin-top: 10px;
        }
        .name-text {
          font-weight: bold;
          font-size: 42px;
          color: #ffffff;
          margin-bottom: 12px;
        }
        .name-type {
          font-size: 28px;
          color: #ffffff;
        }
      }

      .bottom-center {
        width: 600px;
        height: 150px;
        line-height: 150px;
        background: linear-gradient(90deg, #ffdcbe 0%, #f8bd82 100%);
        box-shadow: 0px 6px 20px 12px rgba(101, 0, 2, 0.19);
        border-radius: 75px;
        font-weight: bold;
        font-size: 52px;
        text-align: center;
        color: #a80407;
        cursor: pointer;
      }

      .bottom-right {
        width: 350px;
        height: 120px;
        background: #ffffff;
        border-radius: 69px;
        font-weight: 500;
        font-size: 36px;
        color: #a80407;
        text-align: center;
        padding-top: 18px;
      }

      .empty {
        width: 230px;
      }
    }
  }
}
</style>
<style lang="scss">
.answer-page-container {
  .el-radio-group {
    display: flex;
    .el-radio {
      display: flex;
      align-items: center;
    }
  }
  .el-checkbox-group {
    display: flex;
    .el-checkbox {
      display: flex;
      align-items: center;
    }
  }
  .el-radio__input.is-checked .el-radio__inner::after,
  .el-checkbox__input.is-checked .el-checkbox__inner::after {
    content: "";
    width: 20px;
    height: 10px;
    border: 4px solid white;
    border-top: transparent;
    border-right: transparent;
    text-align: center;
    display: block;
    position: absolute;
    top: 13px;
    left: 13px;
    vertical-align: middle;
    transform: rotate(-45deg);
    border-radius: 0px;
    background: none;
  }
  .custom-dialog {
    .el-dialog__header {
      display: none;
    }
    .el-dialog__body {
      padding: 0;
      height: 1000px;
      .el-icon-circle-close {
        position: absolute;
        top: -78px;
        right: -78px;
        font-size: 100px;
        color: #fff;
        cursor: pointer;
      }
      .dialog-body-content-base-style {
        height: 100%;
      }
    }
  }
}
</style>
